import React from "react";
import { useSelector } from 'react-redux';

import { RootState } from '../store';
import '../css/main.css';
import Home from './Home';
import Contact from './Contact';
import Services from './Services';
import Research from './Research';

const Content = () => {
  const { homeCurrent, servicesCurrent, researchCurrent, contactCurrent } = useSelector((state: RootState) => state.sections);
  return (
    <section>
      {homeCurrent === "current" ? <Home /> : ''}
      {servicesCurrent === "current" ? <Services /> : ''}
      {researchCurrent === "current" ? <Research /> : ''}
      {contactCurrent === "current" ? <Contact /> : ''}
    </section>
  )
}

export default Content;